import "./styles.css";

function VaultEntry(props) {
  return (
    <li>
      <div>
        <table>
          <tr>
            <td>{props.key}</td>
            <td>{props.value}</td>
          </tr>
        </table>
      </div>
    </li>
  );
}

function loginToVault(token) {
  var https = require("follow-redirects").https;

  var options = {
    method: "POST",
    hostname: "or.vault.comcast.com",
    path: "/v1/auth/github_convoy/login",
    headers: {
      "Content-Type": "application/json"
    },
    maxRedirects: 20
  };

  var req = https.request(options, function (res) {
    var chunks = [];

    res.on("data", function (chunk) {
      chunks.push(chunk);
    });

    res.on("end", function (chunk) {
      var body = Buffer.concat(chunks);
      console.log(body.toString());
    });

    res.on("error", function (error) {
      console.error(error);
    });
  });

  var postData = JSON.stringify({
    token: "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"
  });

  req.write(postData);

  req.end();
}

function getVaultEntries() {
  // Returns values in the for of {key: "key", value: "value"}
  loginToVault({});
  return [
    {
      key: "test",
      value: "value"
    }
  ];
}

export default function App() {
  // Fetch values from vault to display on screen
  const vault_values = getVaultEntries();

  return (
    <div className="App">
      <h1>Vault Secrets List</h1>
      <ul>
        {vault_values.map((entry) => (
          <VaultEntry key={entry.key} value={entry.value} />
        ))}
      </ul>
    </div>
  );
}
